import React, { useState } from "react"
import { navigate } from "gatsby"
import { useFlexSearch } from "react-use-flexsearch"
import { useSearchIndex } from "StaticQueries/search-index"
import Layout from "Components/layouts/page"

export default function SearchPage() {
  const searchPathName = `/search`
  const searchHintsUrlPrefix = `?q=`
  const { index, store } = useSearchIndex()
  const [query, setQuery] = useState("")

  const results = useFlexSearch(query, index, store)

  return (
    <>
      <Layout>
        <h1>Search page</h1>
        <input
          className="form-control"
          id="search"
          type="search"
          name="query"
          placeholder="Search all posts"
          value={query}
          onChange={event => {
            navigate(
              `${searchPathName}${searchHintsUrlPrefix}${event.target.value}`,
              {
                state: { query },
              }
            )
            setQuery(event.target.value)
          }}
        ></input>
        <h1>Results</h1>
        <ul>
          {results.map(result => (
            <li key={result.id}>
              <a href={result.slug}>{result.title}</a>
            </li>
          ))}
        </ul>
      </Layout>
    </>
  )
}
